<template>
    <div>
        <el-button 
        type="primary" 
        @click="createBlog"
        >创建博客</el-button>
        <el-table
        :data="tableData"
        border
        style="width: 100%">
        <el-table-column
        prop="id"
        label="序号"
        width="180">
        </el-table-column>
        <el-table-column
        label="封面"
        width="180">
            <template slot-scope="scope">
                    <img class="pic" :src="scope.row.imgurl" alt="">
            </template>
        </el-table-column>
        <el-table-column
        prop="title"
        label="标题"
        width="180">
        </el-table-column>
        <el-table-column
        label="操作"
        width="100">
        <template slot-scope="scope">
        <el-button @click="upDataBlog(scope.row.id)" type="text" size="small">编辑</el-button>
        <el-button @click="deleteBlog(scope.row.id)" type="text" size="small">删除</el-button>
        </template>
        </el-table-column>
        </el-table>
    </div>
</template>

<script>
import request from "../utlis/request";
  export default {
    data() {
      return {
        tableData:[]
      }
    },
    methods:{
        getBlogList(){
            request.get(`/blog`).then((res)=>{
                this.tableData = res.data;
                console.log(res.data);
            })
        },
        
        deleteBlog(id){
            request.delete(`/blog/${id}`).then(()=>{
                this.getBlogList();
            })
        },
        createBlog(){
            this.$router.push("/createBlog")
        },
        upDataBlog(id){
            this.$router.push(`/editBlog/${id}`)
        },
    },
    created(){
        this.getBlogList();
    }
  }
</script>

<style scoped>
    .pic {
        width: 100px;
        height: 100px;
    }
</style>