<template>
    <div class="addSections">
    <!-- Form -->
<el-button type="btn" @click="createSection">添加节</el-button>
    <el-table
    :data="tableData"
    border
    style="width: 100%">
    <el-table-column
    prop="id"
    label="序号"
    width="180">
    </el-table-column>
    <el-table-column
    prop="title"
    label="标题"
    width="180">
    </el-table-column>
    <el-table-column
        label="操作"
        width="100">
        <template slot-scope="scope">
        <el-button @click="updateSection(scope.row.id)" type="text" size="small">编辑</el-button>
        <el-button @click="deleteSection(scope.row.id)" type="text" size="small">删除</el-button>
        </template>
    </el-table-column>
    </el-table>
    </div>
</template>

<script>
import request from "../utlis/request";
  export default {
    data() {
      return {
        tableData:[],
        title:'',
      };
    },
    computed:{
        chapter_id(){
            return this.$route.query.chapter_id;
        }
    },
    methods:{
        getSection(){
            request.get(`/section?chapter_id=${this.chapter_id}`).then((res)=>{
                this.tableData = res.data.data;
                console.log(res.data.data);
        })
        },
        deleteSection(id){
            request.delete(`/section/${id}`).then((res)=>{
                this.getSection();
            })
        },
        createSection(){
            this.$router.push(`/createSection?chapter_id=${this.chapter_id}`);
        },
        updateSection(id){
            this.$router.push(`/section/${id}?chapter_id=${this.chapter_id}`);
        }
    },
    created(){
        this.getSection();
    }
  };
</script>
