<template>
    <div class="createContent">
         <el-button 
            type="primary" 
            @click="save"
            >保存</el-button>
        <div>
            <el-input v-model="title" placeholder="请输入标题"></el-input>
            <mavon-editor @change="changText" v-model="md_text"/>
        </div>
    </div>
</template>

<script>
import request from "../utlis/request";
export default {
    data(){
        return{
            chapter_id:this.$route.query.chapter_id,
            title:"",
            md_text:"",
            html_text:""
        }
    },
    computed:{
        // chapter_id(){
        //     return 
        // },
        id(){
            return this.$route.params.id
        }
    },
    methods:{
        save(){
            if(this.id){
                this.updateSection();
            }else{
                this.createSection();
            }
        },
        createSection(){
            request.post(`/section`,{
                title:this.title,
                md_text:this.md_text,
                html_text:this.html_text,
                chapter_id:this.chapter_id
                })
            .then(()=>{
                this.$router.push(`/section?chapter_id=${this.chapter_id}`)
            })
        },
        updateSection(){
            request.put(`/section/${this.id}`,{
                title:this.title,
                md_text:this.md_text,
                html_text:this.html_text,
                chapter_id:this.chapter_id
            }).then((res)=>{
                this.$router.push(`/section?chapter_id=${this.chapter_id}`)
            })
        },
        changText(md_text,html_text){
            this.md_text = md_text;
            this.html_text = html_text;
        },
        getSectionList(){
            request.get(`/section?chapter_id=${this.chapter_id}`)
            .then((res) =>{
                let section = res.data.data[0];
                console.log(res.data.data[0]);
                console.log(section.md_text);
                this.title = section.title;
                this.md_text = section.md_text;
                this.html_text = section.html_text;
                this.chapter_id = section.chapter_id;
            })
        }
    },

    created(){
        console.log(this.id);
        if(this.id){
            this.getSectionList();
        }else{
            console.log("没拿到数据");
        }
    }
}   
</script>