<template>
    <div class="createBook">
    <!-- Form -->
<el-button type="btn" @click="addBook">添加书籍</el-button>
    <el-table
    :data="tableData"
    border
    style="width: 100%">
    <el-table-column
    prop="id"
    label="序号"
    width="180">
    </el-table-column>
    <el-table-column
    label="封面"
    width="180">
        <template slot-scope="scope">
            <img class="pic" :src="scope.row.imgurl" alt="">
        </template>
    </el-table-column>
    <el-table-column
    prop="title"
    label="标题"
    width="180">
    </el-table-column>
    <el-table-column
        label="操作"
        width="100">
        <template slot-scope="scope">
        <el-button @click="createChapter(scope.row.id)" type="text" size="small">查看章</el-button>
        <el-button @click="deleteBook(scope.row.id)" type="text" size="small">删除</el-button>
        </template>
    </el-table-column>
    </el-table>
<el-dialog title="添加书" :visible.sync="dialogVisible" width="30%">
    <div class="cover">
        封面:
            <el-upload
            :action="getUploadUrl()"
            list-type="picture-card"
            :on-remove="handleRemove"
            :limit="1"
            :on-success="success"
            :file-list="fileList"
            :on-exceed="handleExceed"
            >
            <i class="el-icon-plus"></i>
            </el-upload>
    </div>
    <el-form>
            <el-form-item label="标题">
                <el-input v-model="title"></el-input>
            </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="createBook">确 定</el-button>
    </div>
</el-dialog>
    </div>
</template>

<script>
import request from "../utlis/request";
  export default {
    data() {
      return {
        videoIsShow: false,
        tableData:[],
        fileList:[],
        dialogVisible: false,
        title:'',
        imgurl:'',
      };
    },
    methods:{
        addBook(){
            this.dialogVisible = true;
            this.title = "";
            this.iframe = "";
            this.isEdit = false;
        },
        getBook(){
            request.get(`/book`).then((res)=>{
                this.tableData = res.data;
                console.log(res.data);
        })
        },
        createBook(){
            request.post(`/book`,{
                title:this.title,
                imgurl:this.imgurl
        }).then((res)=>{
            this.getBook();
            this.dialogVisible = false;
        })
        },
        deleteBook(id){
            request.delete(`/book/${id}`).then((res)=>{
                this.getBook();
            })
        },
        createChapter(id){
            this.$router.push(`/chapter?book_id=${id}`);
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handleExceed(file,fileList){
            this.$message.warning("只能上传一张图片");
        },
        success(res){
            console.log(res);
            this.imgurl = res.data.file;
        },
        getUploadUrl() {
            return process.env.VUE_APP_UPLOAD_API;
        },
    },
    created(){
        this.getBook();
    }
  };
</script>

<style scoped>
  .pic {
      width: 100px;
      height: 100px;
  }
</style>