<template>
    <div class="createVideo">
    <!-- Form -->
<el-button type="btn" @click="addVideo">添加视频</el-button>
    <el-table
    :data="tableData"
    border
    style="width: 100%">
    <el-table-column
    prop="id"
    label="序号"
    width="180">
    </el-table-column>
    <el-table-column
    label="封面"
    width="180">
        <template slot-scope="scope">
            <img class="pic" :src="scope.row.imgurl" alt="">
        </template>
    </el-table-column>
    <el-table-column
    prop="title"
    label="标题"
    width="180">
    </el-table-column>
    <el-table-column label="详情" width="100">
        <template slot-scope="scope">
            <el-button @click="getDetail(scope.row.iframe)" type="text" size="small">浏览视频</el-button>
        </template>
    </el-table-column>
    <el-table-column
        label="操作"
        width="100">
        <template slot-scope="scope">
        <el-button @click="deleteVideo(scope.row.id)" type="text" size="small">删除</el-button>
        </template>
    </el-table-column>
    </el-table>
    <el-dialog title="视频" :visible.sync="videoIsShow" width="800px">
            <div class="test" v-html="video_url"></div>
    </el-dialog>
<el-dialog title="添加视频" :visible.sync="dialogVisible" width="30%">
    <div class="cover">
        封面:
            <el-upload
            :action="getUploadUrl()"
            list-type="picture-card"
            :on-remove="handleRemove"
            :limit="1"
            :on-success="success"
            :file-list="fileList"
            :on-exceed="handleExceed"
            >
            <i class="el-icon-plus"></i>
            </el-upload>
    </div>
    <el-form>
            <el-form-item label="视频标题">
                <el-input v-model="title"></el-input>
            </el-form-item>
            <el-form-item label="视频地址">
                <el-input v-model="iframe"></el-input>
            </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="createVideo">确 定</el-button>
    </div>
</el-dialog>
    </div>
</template>

<script>
import request from "../utlis/request";
  export default {
    data() {
      return {
        videoIsShow: false,
        tableData:[],
        fileList:[],
        dialogVisible: false,
        title: '',
        iframe:'',
        imgurl:'',
        video_url:"<h1>123</h1>"
      };
    },
    methods:{
        addVideo(){
            this.dialogVisible = true;
            this.title = "";
            this.iframe = "";
            this.isEdit = false;
        },
        getVideo(){
            request.get(`/video`).then((res)=>{
                this.tableData = res.data;
                console.log(res.data);
        })
        },
        createVideo(){
            request.post(`/video`,{
                title:this.title,
                iframe:this.iframe,
                imgurl:this.imgurl
        }).then((res)=>{
            this.getVideo();
            this.dialogVisible = false;
        })
        },
        deleteVideo(id){
            request.delete(`/video/${id}`).then((res)=>{
                this.getVideo();
            })
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handleExceed(file,fileList){
            this.$message.warning("只能上传一张图片");
        },
        success(res){
            console.log(res);
            this.imgurl = res.data.file;
        },
        getDetail(video_url) {
            this.videoIsShow = true;
            this.video_url = video_url;
            console.log(video_url)
        },
        getUploadUrl() {
            return process.env.VUE_APP_UPLOAD_API;
        },
    },
    created(){
        this.getVideo();
    }
  };
</script>

<style scoped>
  .pic {
      width: 100px;
      height: 100px;
  }
  .createVideo iframe{
    width:100%;
    height:500px;
}
</style>