<template>
    <div class="createBook">
    <!-- Form -->
<el-button type="btn" @click="addSection">添加章</el-button>
    <el-table
    :data="tableData"
    border
    style="width: 100%">
    <el-table-column
    prop="id"
    label="序号"
    width="180">
    </el-table-column>
    <el-table-column
    prop="title"
    label="标题"
    width="180">
    </el-table-column>
    <el-table-column
        label="操作"
        width="100">
        <template slot-scope="scope">
        <el-button @click="createSection(scope.row.id)" type="text" size="small">创建节</el-button>
        <el-button @click="deleteSection(scope.row.id)" type="text" size="small">删除</el-button>
        </template>
    </el-table-column>
    </el-table>
<el-dialog title="添加节" :visible.sync="dialogVisible" width="30%">
    <el-form>
            <el-form-item label="标题">
                <el-input v-model="title"></el-input>
            </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="createChapter">确 定</el-button>
    </div>
</el-dialog>
    </div>
</template>

<script>
import request from "../utlis/request";
  export default {
    data() {
      return {
        videoIsShow: false,
        tableData:[],
        dialogVisible: false,
        title:'',
      };
    },
    computed:{
        book_id(){
            return this.$route.query.book_id;
        }
    },
    methods:{
        addSection(){
            this.dialogVisible = true;
            this.title = "";
            this.isEdit = false;
        },
        getChapter(){
            request.get(`/chapter?book_id=${this.book_id}`).then((res)=>{
                this.tableData = res.data.data;
                console.log(res.data.data);
        })
        },
        createChapter(){
            request.post(`/chapter`,{
                title:this.title,
                book_id:this.book_id,
        }).then((res)=>{
            this.getChapter();
            this.dialogVisible = false;
        })
        },
        deleteSection(id){
            request.delete(`/chapter/${id}`).then((res)=>{
                this.getChapter();
            })
        },
        createSection(id){
            this.$router.push(`/section?chapter_id=${id}`);
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handleExceed(file,fileList){
            this.$message.warning("只能上传一张图片");
        },
        success(res){
            console.log(res);
        },
    },
    created(){
        this.getChapter();
    }
  };
</script>
