<template>
    <div class="createBlog">
         <el-button 
            type="primary" 
            @click="createBlog"
            >保存</el-button>
        <div>
            封面:
            <el-upload
            :action="getUploadUrl()"
            list-type="picture-card"
            :on-remove="handleRemove"
            :limit="1"
            :on-success="success"
            :file-list="fileList"
            :on-exceed="handleExceed"
            >
            <i class="el-icon-plus"></i>
            </el-upload>
            <el-input v-model="content.title" placeholder="请输入标题"></el-input>
            <mavon-editor @change="changText" v-model="content.md_text"/>
        </div>
    </div>
</template>

<script>
import request from "../utlis/request";
export default {
    data(){
        return{
            id:this.$route.params.id,
            fileList:[],
            content:{
                imgurl:"",
                title:"",
                md_text:"",
                html_text:""
            }
        }
    },
    methods:{
        createBlog(){
            request.put(`/blog/${this.id}`,{content:this.content})
            .then((res)=>{
                this.$router.push("/blog")
            })
        },
        changText(value,render){
            this.content.md_text = value;
            this.content.html_text = render;
        },
        // getBlogList(){
        //     axios.get(`http://127.0.0.1:7001/blog/${this.id}`,{content:this.content})
        //     .then((res)=>{
        //         this.content.imgurl = res.data.imgurl;
        //         this.content.md_text = res.data.md_text;
        //         this.content.html_text = res.data.html_text;
        //         this.content.title = res.data.title;
        //         console.log(res.data);
        //     })
        // },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handleExceed(file,fileList){
            this.$message.warning("只能上传一张图片");
        },
        success(res){
            console.log(res);
            this.content.imgurl = res.data.file;
        },
        getUploadUrl() {
            return process.env.VUE_APP_UPLOAD_API;
        },
    },
    created(){
        // this.getBlogList();
        if(this.id){
            request.get(`/blog/${this.id}`).then((res)=>{
                let blog = res.data;
                this.content.title = blog.title;
                this.fileList = [
                    {url:blog.imgurl}
                ]
                this.content.imgurl = blog.imgurl;
                this.content.md_text = blog.md_text;
                this.content.html_text = blog.html_text;
            })
        }
    }
}   
</script>