<template>
    <div>
        <el-button 
        type="primary" 
        @click="addRe"
        >添加资源</el-button>
        <el-table
        :data="tableData"
        border
        style="width: 100%">
        <el-table-column
        prop="id"
        label="序号"
        width="180">
        </el-table-column>
        <el-table-column
        prop="title"
        label="标题"
        width="180">
        </el-table-column>
        <el-table-column
        prop="code"
        label="提取码"
        width="180">
        </el-table-column>
        <el-table-column
        prop="downloadurl"
        label="下载地址"
        width="180">
        </el-table-column>
        <el-table-column
        label="操作"
        width="100">
        <template slot-scope="scope">
        <el-button @click="deleteBlog(scope.row.id)" type="text" size="small">删除</el-button>
        </template>
        </el-table-column>
        </el-table>
        <el-dialog title="添加资源" :visible.sync="dialogVisible" width="30%">
        <el-form>
            <el-form-item label="标题">
                <el-input v-model="content.title"></el-input>
            </el-form-item>
            <el-form-item label="提取码">
                <el-input v-model="content.code"></el-input>
            </el-form-item>
            <el-form-item label="下载地址">
                <el-input v-model="content.downloadurl"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="createRe">确 定</el-button>
        </div>
        </el-dialog>
    </div>
</template>

<script>
import request from "../utlis/request";
  export default {
    data() {
      return {
        tableData:[],
        content:{
                title:"",
                code:"",
                downloadurl:"",
            },
        dialogVisible: false,
      }
    },
    methods:{
        addRe(){
            this.dialogVisible = true;
            this.title = "";
            this.code = "";
            this.downloadurl = "",
            this.isEdit = false;
        },
        getReList(){
            request.get(`/resource`).then((res)=>{
                this.tableData = res.data;
                console.log(res.data);
            })
        },
        deleteBlog(id){
            request.delete(`/resource/${id}`).then(()=>{
                this.getReList();
            })
        },
        createRe(){
            request.post(`/resource`,{content:this.content})
            .then(()=>{
                this.getReList();
                this.dialogVisible = false;
            })
        },
    },
    created(){
        this.getReList();
    }
  }
</script>

