<template>
    <div class="createBlog">
         <el-button 
            type="primary" 
            @click="createBlog"
            >保存</el-button>
        <div>
            封面:
            <el-upload
            :action="getUploadUrl()"
            list-type="picture-card"
            :on-remove="handleRemove"
            :limit="1"
            :on-success="success"
            :file-list="fileList"
            :on-exceed="handleExceed"
            >
            <i class="el-icon-plus"></i>
            </el-upload>
            <el-input v-model="content.title" placeholder="请输入标题"></el-input>
            <mavon-editor @change="changText" v-model="content.md_text"/>
        </div>
    </div>
</template>

<script>
import request from "../utlis/request";
export default {
    data(){
        return{
            fileList:[],
            content:{
                imgurl:"",
                title:"",
                md_text:"",
                html_text:""
            }
        }
    },
    methods:{
        createBlog(){
            request.post(`/blog`,{content:this.content})
            .then(()=>{
                this.$router.push("/blog")
            })
        },
        changText(value,render){
            this.content.md_text = value;
            this.content.html_text = render;
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handleExceed(file,fileList){
            this.$message.warning("只能上传一张图片");
        },
        success(res){
            console.log(res);
            this.content.imgurl = res.data.file;
        },
        getUploadUrl() {
            return process.env.VUE_APP_UPLOAD_API;
        },
    }
}   
</script>